@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700);
@import url(https://fonts.googleapis.com/css?family=Poppins:400);
html {
  font-size: 100%;
} /*16px*/

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  line-height: 1.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 3rem 0 1rem;
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  line-height: 1.3;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  margin-top: 0.5rem;
  font-size: 2.441rem;
}

h3,
.h3 {
  font-size: 1.953rem;
}

h4 {
  margin: 0;
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text_small {
  font-size: 0.8rem;
}

a {
  color: #002626;
}

button {
  font-family: 'Poppins', sans-serif;
}

button,
a.btn,
input[type='submit'] {
  -webkit-appearance: none;
  display: inline-block;
  background-color: #002626;
  color: #fff;
  padding: 0.75rem 1rem;
  line-height: 1.5;
  border-radius: 0.25em;
  border: none;
  text-decoration: none;
  font-size: 1rem;
  margin: 0.5rem 0;
}

button:hover,
a.btn:hover,
input[type='submit']:hover {
  cursor: pointer;
  background-color: #dddc4e;
  color: #002626;
}
button:active,
a.btn:active,
input[type='submit']:active {
  background-color: #f6cfb2;
}

input {
  padding: 0.5rem;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

input[type='email'],
input[type='text'] {
  display: block;
  margin: 0.5rem auto 1rem;
  line-height: 1.5;
  font-size: 1.25rem;
  border: 1px solid;
  border-radius: 0.25em;
}

#top-nav .nav-contents,
#body-container,
#footer > div {
  max-width: 1200px;
  margin: 0 auto;
}

#body-container {
  margin-top: 6rem;
  padding: 0 1rem;
  min-height: calc(90vh - 6rem);
}

.hero {
  padding: 30vh 0;
}

.hero-bg-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
}

.hero-content {
  z-index: 1;
  position: relative;
}

.hero-content a.btn {
  margin-top: 0.5rem;
}

.star {
  /* lighter */
  /* color: #F1B71C; */
  
  color: #E8C71B;
  
  /* for more contrast */
  /* color: #AA6100; */

  /* makes the star bigger */
  font-size: 21px;
}

#footer {
  background: #f6cfb2;
  text-align: center;
  z-index: 1;
  position: relative;
  -webkit-clip-path: polygon(0% 25%, 50% 0%, 100% 25%, 100% 100%, 0% 100%);
          clip-path: polygon(0% 25%, 50% 0%, 100% 25%, 100% 100%, 0% 100%);
}

@media screen and (min-width: 800px) {
  #footer {
    -webkit-clip-path: polygon(0% 50%, 50% 0%, 100% 50%, 100% 100%, 0% 100%);
            clip-path: polygon(0% 50%, 50% 0%, 100% 50%, 100% 100%, 0% 100%);
  }
}

#footer > div {
  padding: 8rem 0 4rem;
}

#footer form {
  width: 50%;
  margin: 0 auto;
  text-align: left;
}

#footer form label {
  margin: 0;
  display: inline-block;
}

#footer form input {
  font-size: 1.25rem;
  margin: 0.5rem auto;
}

#top-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background: #fff;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.05);
}

#top-nav .nav-contents {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 1rem;
}

#top-nav a {
  text-decoration: none;
}

#top-nav a:active {
  text-decoration: none;
}

nav.show-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  nav.show-mobile {
    display: none;
  }
  nav.show-desktop {
    display: block;
  }
}

#home-link,
#top-right-links {
  align-items: center;

  display: flex;
}

#home-link {
  font-size: 1.953rem;
}
#home-link img {
  max-height: 3rem;
  width: auto;
  margin-right: 0.5rem;
}

#top-right-links {
  justify-content: flex-end;
}
#top-right-links a {
  margin-top: 3px;
  padding: 0.5rem;
  font-size: 1.25em;
  border-bottom: 3px solid #fff;
}
#top-right-links a:hover {
  border-color: #dae3e4;
}
#top-right-links a:active {
  border-color: #f6cfb2;
}

.about-page {
  text-align: left;
  margin: 0 auto;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}

.about-page > div {
  flex: 1 1;
}

.about-info {
  max-width: 45rem;
  margin: 0 auto;
  padding: 0 1rem 1rem;
}

@media screen and (min-width: 1200px) {
  .about-page {
    flex-direction: row;
  }

  .about-info {
    padding: 0 2rem 0 0;
  }
}

ul.employee-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 500px;
  margin: 0 auto;
}

ul.employee-list li {
  flex: 0 0 50%;
  padding: 0 1rem;
  box-sizing: border-box;
  text-align: center;
}

ul.employee-list li a {
  text-decoration: none;
}

ul.employee-list li a:hover {
  opacity: 0.8;
}

ul.employee-list li h5 {
  margin: 0 0 2rem;
}

ul.employee-list li img {
  width: 100%;
  border-radius: 50%;
}

/* for selenium driver to click */
.employee-name {

}

.cart-container {
  text-align: center;
}

ul.cart-list {
  padding: 0;
}

.cart-item {
  border-top: 1px solid #002626;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  justify-content: space-between;
  text-align: right;
}

@media screen and (min-width: 600px) {
  .cart-item {
    flex-direction: row;
  }
}

.cart-item > * {
  flex: 1 1;
}

.cart-item a {
  text-align: left;
  text-decoration: none;
}

.cart-item img {
  width: 100%;
  height: auto;
  display: block;
}
@media screen and (min-width: 600px) {
  .cart-item img {
    width: calc(100% - 1rem);
  }
}

.cart-item h4 {
  margin: 1rem 0 0.5rem;
}

.cart-item p {
  margin: 0.5rem 0;
}

.quantity-adjust span {
  margin: 0 0.5rem;
}

h3.cart-subtotal {
  margin: 0 0 1rem;
}

a.checkout-btn {
  margin-bottom: 3rem;
}

.checkout-container {
  text-align: center;
  margin-bottom: 3rem;
}

.checkout-container h2 {
  border-bottom: 1px solid;
  padding: 0 0 1rem;
}

.checkout-form {
  margin: 2rem auto;
  max-width: 80%;
  text-align: left;
}
@media screen and (min-width: 1200px) {
  .checkout-form {
    max-width: 50%;
  }
}

.checkout-form input {
  display: block;
  width: 100%;
}
.checkout-form input[type='checkbox'] {
  display: inline-block;
  width: auto;
}

.checkout-form h4 {
  margin: 3rem auto 1rem;
}

input[type='submit'].complete-checkout-btn {
  font-size: 1.25rem;
  margin-top: 3rem;
}

.checkout-container-complete {
  text-align: center;
  margin: 0 auto;
  padding-top: 20vh;
  max-width: 35rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.employee-page {
  display: flex;
  justify-content: center;
  padding-top: 3rem;
}
.employee-image {
  width: 25%;
}
.employee-page img {
  width: 100%;
  border-radius: 50%;
}

.employee-info {
  width: 50%;
  padding-left: 2rem;
  box-sizing: border-box;
}

.product-layout {
  display: flex;
  padding-top: 2rem;
}
.product-layout > div {
  flex: 1 1;
}

.product-layout img {
  width: 100%;
}

.product-info {
  margin-left: 2rem;
  border-top: 1px solid;
}

.product-info h1 {
  margin-top: 0.5rem;
}

.products-list {
  list-style: none;
  padding: 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.products-list li {
  flex-basis: 100%;
  margin-bottom: 2rem;
}

@media screen and (min-width: 600px) {
  .products-list li {
    flex-basis: calc(50% - 1rem);
  }
}

.products-list li > div {
  padding: 1.5rem;
  border-radius: 0.25rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.07), 0 6px 20px 0 rgba(0, 0, 0, 0.05);
}

.products-list li img {
  width: 100%;
}

.products-list li a {
  text-decoration: none;
}

.loader-container {
  text-align: center;
}
